* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.headerBox {
  position: fixed;
  width: 100%;
  top: 0px;
  background: rgb(255, 255, 255);
  z-index: 2;
  box-shadow: rgb(255 255 255) 0px -1px 0px 1px;
}

.header {
  overflow: hidden;
  background-color: #f1f1f1;
  padding: 20px 10px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.header a {
  margin-right: 20px;
  float: left;
  color: black;
  text-align: center;
  padding: 12px;
  text-decoration: none;
  font-size: 18px;
  line-height: 25px;
  border-radius: 4px;
  font-weight: bold;
}

.header a.logo {
  font-size: 25px;
  font-weight: bold;
}

.header a:hover {
  /* background-color: #ddd; */
  color: #096ffa;
}

.header a.active {
  color: #096ffa;
}

@media screen and (max-width: 500px) {
  .header a {
    float: none;
    display: block;
    text-align: left;
  }

  .headerRight {
    float: none;
  }
}
